/* converter *
********/
.b-converter {
  align-self: center;
  margin: auto;
  width: 100%; }

.converter-field {
  display: flex;
  flex-wrap: wrap;
  width: 100%; }

.converter-label {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

.converter-input {
  display: flex;
  flex: 2;
  width: 100%;
  border-radius: 2px;
  border: none; }
  @media (max-width: 768px) {
    .converter-input {
      min-width: 100%;
      margin-bottom: 15px; } }

.converter-input-inner {
  padding: 30px 40px;
  width: 100%;
  font: 400 20px/1.5 'Gingham Variable', BlinkMacSystemFont , sans-serif;
  font-variation-settings: "wdth" 600;
  border-radius: 10px;
  border: none;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.2), -3px -3px 10px rgba(0, 0, 0, 0.1);
  background: #F2F2F2; }
  @media (max-width: 768px) {
    .converter-input-inner {
      padding: 20px;
      text-align: center; } }
  @media (max-width: 1440px) {
    .converter-input-inner {
      padding: 20px 25px;
      font-size: 16px; } }

.converter-btn {
  flex: 1;
  margin-left: 30px;
  padding: 30px 40px;
  background: #181818;
  overflow: hidden;
  border: none;
  border-radius: 10px;
  transition: background 250ms ease-out;
  cursor: pointer; }
  .converter-btn:hover, .converter-btn:focus {
    background: #66B2E4; }
  .converter-btn:hover .converter-btn-text,
  .converter-btn:focus .converter-btn-text {
    color: #2A2A2A; }
  @media (max-width: 768px) {
    .converter-btn {
      margin-left: 0;
      padding: 20px; } }
  @media (max-width: 1440px) {
    .converter-btn {
      padding: 20px 25px; } }

.converter-btn-text {
  font: 400 18px/1.5 'Gingham Variable', BlinkMacSystemFont , sans-serif;
  font-variation-settings: "wdth" 600;
  text-transform: uppercase;
  color: #ededed;
  transition: color 250ms ease-out; }
  @media (max-width: 768px) {
    .converter-btn-text {
      font-size: 14px; } }
  @media (max-width: 1440px) {
    .converter-btn-text {
      font-size: 16px; } }
