/* page *
********/
.l-page {
  width: 100%;
  height: 100%; }

.page-inner {
  display: flex;
  flex-direction: column;
  height: 100%; }

.page-main {
  flex: 1;
  width: 100%;
  background: #ededed; }

.page-section {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1; }
  .page-section:after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    width: 100%;
    height: 100%;
    background: url("../media/camping-area.jpg") 100% no-repeat;
    background-size: cover;
    z-index: -1; }

.page-section-inner {
  display: flex;
  max-width: 800px;
  margin: auto;
  padding: 0 25px;
  min-height: 100%;
  z-index: 1; }
  @media (max-width: 768px) {
    .page-section-inner {
      max-width: 500px; } }

.page-footer {
  flex-shrink: 0;
  width: 100%;
  background: #E42613; }

.page-footer-inner {
  padding: 25px; }

.page-footer-text {
  font-family: 'Gingham Variable', BlinkMacSystemFont , sans-serif;
  font-variation-settings: "wght" 300, "wdth" 250;
  font-size: 16px;
  color: #F2F2F2; }
  @media (max-width: 400px) {
    .page-footer-text {
      font-size: 12px; } }
  @media (max-width: 400px) {
    .page-footer-text {
      font-size: 14px; } }
