/* pageheader *
********/
.b-pageheader {
  width: 100%;
  background: #ffffff; }

.pageheader-inner {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 20px 25px; }

.pageheader-brand {
  width: 60px;
  height: 60px;
  z-index: 5; }

.pageheader-title, .pageheader-text {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  padding: 0 25px;
  text-align: center;
  font: 600 30px/1.3 'Gingham Variable', BlinkMacSystemFont , sans-serif;
  font-variation-settings: "wdth" 600;
  color: #5B5B5B; }
  @media (max-width: 768px) {
    .pageheader-title, .pageheader-text {
      font-size: 24px;
      max-width: 275px; } }
  @media (max-width: 500px) {
    .pageheader-title, .pageheader-text {
      font-size: 20px;
      left: auto;
      max-width: 200px;
      text-align: right; } }

.pageheader-text {
  font-size: 42px; }
